const headerTemplateCreator = () => `
        <nav class="text-slate-50 flex justify-between items-center gap-1 w-full h-16 px-3 md:px-5 bg-slate-800 shadow-md shadow-slate-900">
            <div class="start relative">
                <div class="flex items-center">
                    <a class="text-2xl font-bold ml-1" href="">RTH</a>
                </div>
            </div>
            <div class="end relative flex items-center justify-end w-full">

                <div class="container-menu text-sm md:text-base md:mr-10 mx-auto">
                    <ul class="flex items-center md:gap-2">
                        <li><a class="flex items-center justify-start h-[44px] w-full hover:bg-slate-700 py-1 px-2 transition-all ease-in-out whitespace-nowrap rounded" href="#/trending/">Trending</a></li>
                        <li><a class="flex items-center justify-start h-[44px] w-full hover:bg-slate-700 py-1 px-2 transition-all ease-in-out whitespace-nowrap rounded" href="#/upcomming/">Upcomming</a></li>
                        <li><a class="flex items-center justify-start h-[44px] w-full hover:bg-slate-700 py-1 px-2 transition-all ease-in-out whitespace-nowrap rounded" href="#/tvshow/">Tv Show</a></li>
                    </ul>
                </div>

                <div class="button-container">
                    <button class="buttonTrigger button-search w-[44px] aspect-square flex items-center justify-center hover:bg-slate-700" title="Button search"><i class='bi bi-search text-lg icon-search w-full h-full flex items-center justify-center'></i></button>
                </div>
                <div class="container-search max-w-[500px] bg-slate-800 min-h-[200px] absolute top-[54px] overflow-hidden w-0 transition-all ease-in-out duration-500 z-10  shadow-md shadow-slate-900">
                    <div class="search-bar w-auto flex items-center justify-center relative m-3">
                        <input class="search-input h-[44px] w-full text-slate-900 p-3 text-sm" type="search" placeholder="Search">
                        <!--- <button class="searching bg-red-600 hover:bg-red-700 transition-all w-[40px] aspect-square absolute right-[2px] "><i class="bi bi-search"></i></button> --->
                    </div>
                    <div class="text-center text-xs p-2">
                        <p class="text-slate-50 mb-3">Results</p>
                        <div class="result h-[300px] overflow-y-scroll scrollbar-custom">
                        
                        </div>
                    </div>
                </div>
            </div>
        </nav>
    `;

const sliderSplideListItem = (data) => `
    <li class="splide__slide relative before:absolute before:w-full before:bg-gradient-to-t before:from-slate-900 before:via-slate-900/80 before:to-slate-900/0 before:h-[100px] before:left-0 before:bottom-0 before:lg:h-[200px]" title="${data.title ? data.title : data.name}">
        <div class="poster absolute bottom-4 left-6 flex text-slate-50 md:bottom-10 md:left-16">
            <div class="image">
                <picture>
                    <source media="(max-width: 600px)" srcset="https://image.tmdb.org/t/p/w185${data.poster_path}">
                    <img class="object-cover min-h-[100px] max-h-[150px] md:min-h-[150px] md:max-h-[200px] lg:min-h-[220px] lg:max-h-[300px] rounded shadow-lg shadow-slate-900" src="https://image.tmdb.org/t/p/w342${data.poster_path}" alt="poster of ${data.title ? data.title : data.name}">
                </picture>
            </div>
            <a class="title self-end my-1 mx-3 max-w-[300px] md:my-3 lg:my-5" href="#/${data.media}/${data.id}">
                <h1 class="text-lg font-bold md:text-2xl">${data.title ? data.title : data.name}</h1>
                <span class="slider-genre text-xs bg-red-600 text-slate-50 font-medium py-1 px-2 rounded">${String(data.vote_average).substring(0, 3)}/10</span>
            </a>
        </div>
        <picture>
            <source media="(max-width: 768px)" srcset="https://image.tmdb.org/t/p/w780${data.backdrop_path}">
            <source media="(max-width: 1444px)" srcset="https://image.tmdb.org/t/p/w1280${data.backdrop_path}">
            <img class="object-cover object-top w-full h-full rounded-[inherit]" src="https://image.tmdb.org/t/p/w1280${data.backdrop_path}" alt="image of ${data.title ? data.title : data.name}">
        </picture>
    </li>
`;

const asideTemplate = (data) => `
        <a class="list-items flex gap-2 items-center bg-slate-900 rounded mr-2 relative hover:-translate-y-1 transition-all" href="#/${data.media}/${data.id}" title="${data.name ? data.name : data.title}">
            <div class="poster">
                ${data.poster_path ? `
                    <img width="90" height="120" class="object-cover rounded-tl rounded-bl" src="https://image.tmdb.org/t/p/w185${data.poster_path}" alt="image og ${data.name ? data.name : data.title}" loading="lazy">
                ` : `
                    <img width="90" height="120" class="object-cover rounded-tl rounded-bl" src="./assets/not-found-poster-small.webp" alt="image og ${data.name ? data.name : data.title}" loading="lazy">
                `}
            </div>
            <div class="title w-full">
                <h2 class="font-medium">${data.name ? data.name : data.title}</h2>
                <!---<i class="text-xs">${String(data.overview).substring(0, 30)}...</i>--->
            </div>
            <div class="rating flex items-center mr-5">
                <span class="text-xs bg-red-600 text-slate-50 py-1 px-2 rounded font-medium">${String(data.vote_average).substring(0, 3)}/10</span>
            </div>
        </a>
    `;

const listTemplate = (title, pagesName, layout, pagination, link, slug) => `
        <div class="text-slate-50 bg-slate-800 p-2 mb-5 rounded"> 
            <div class="title mb-2 flex items-center justify-between px-2">
                <h2 class="font-bold text-lg">${pagesName === 'genre' ? `Result for ${title}` : title}</h2>
                ${link ? `<a href="#/${slug}/" class="text-xs py-1 px-2 rounded bg-slate-700 hover:bg-slate-600 transition-all">See All</a>` : ''}
            </div>
            <div class="${pagination ? 'overflow-y-scroll h-full md:h-[70vh] scrollbar-custom pt-2 pr-2' : 'overflow-x-scroll scrollbar-custom scrollbar-hide pt-1 lg:pb-2'}">
                <div class="list-card ${layout === 'grid' ? ' grid gap-2 grid-cols-2 md:grid-cols-4 lg:grid-cols-5' : 'flex justify-between '}">

                </div>
            </div>
            ${pagination ? `
                <div class="pagination text-center mt-2 flex items-center justify-center gap-2">
                    <button class="prev bg-red-600 text-slate-50 hover:bg-red-700 text-sm rounded py-1 px-2" title="Previous Page">Prev</button>
                    <input class="number-of-page text-slate-50 w-[50px] h-[28px] border-none text-center text-sm rounded" type="text" name="" id="" value="1" disabled>
                    <button class="next bg-red-600 text-slate-50 hover:bg-red-700 text-sm rounded py-1 px-2" title="Next Page">Next</button>
                </div>
            ` : ''}
        </div>  
    `;

const cardTemplate = (data, pagesName, layout) => `
          <a class="card relative rounded transition-all hover:-translate-y-1" href="#/${data.media}/${data.id}" title="${data.title ? data.title : data.name}">
              <picture>
                ${data.poster_path || data.profile_path ? `
                    <source media="(max-width: 600px)" srcset="https://image.tmdb.org/t/p/w342${data.poster_path ? data.poster_path : data.profile_path}">                    
                    <img class="object-cover ${layout === 'flex' ? 'w-[150px] h-[230px]' : 'w-full h-full'} rounded" src="https://image.tmdb.org/t/p/w342${data.poster_path ? data.poster_path : data.profile_path}" alt="poster of ${data.title ? data.title : data.name}" loading="lazy">
                ` : `
                    <source media="(max-width: 600px)" srcset="./assets/not-found-poster-medium.webp">
                    <img class="object-cover ${layout === 'flex' ? 'w-[150px] h-[230px]' : 'w-full h-full'} rounded" src="./assets/not-found-poster-large.webp" loading="lazy">
                `}
                  
              </picture>
              ${data.media === 'person' ? `
                <div class="name absolute bottom-0 left-0 w-full h-fit overflow-hidden bg-slate-800/50 backdrop-blur-sm flex items-center justify-center lg:h-0">
                    ${data.credit_id ? `
                        <div class="text-center py-2 lg:py-0">
                            <span class="text-xs font-bold block">${data.character}</span>
                            <i class="text-xs block">${data.name}</i>
                        </div>
                    ` : `<span class="text-xs font-medium py-2 md:py-0">${data.name}</span>`}
                </div>
              ` : pagesName === 'genre' ? `
                <span class="bg-red-600 text-slate-50 font-medium px-2 py-1 absolute top-0 right-0 text-xs rounded-tr-[inherit] rounded-bl-[inherit]">${String(data.vote_average).substring(0, 3)}/10</span>
                <span class="bg-red-600 text-slate-50 font-medium px-2 py-1 absolute bottom-0 left-0 text-xs rounded-bl-[inherit] rounded-tr-[inherit]">${data.media.charAt(0).toUpperCase() + data.media.slice(1)}</span>
              ` : data.poster_path === null || data.profile_path === null ? `
                <span class="bg-red-600 text-slate-50 font-medium px-2 py-1 absolute top-0 right-0 text-xs rounded-tr-[inherit] rounded-bl-[inherit]">${String(data.vote_average).substring(0, 3)}/10</span>
                <span class="text-slate-50 absolute left-0 bottom-0 py-2 px-1 text-center text-xs w-full bg-slate-800/50 backdrop-blur">${data.name ? data.name : data.title}</span>
              ` : `
                <span class="bg-red-600 text-slate-50 font-medium px-2 py-1 absolute top-0 right-0 text-xs rounded-tr-[inherit] rounded-bl-[inherit]">${String(data.vote_average).substring(0, 3)}/10</span>
              `}
          </a>
      `;

const seasonTemplate = (data) => `
        <div class=" text-slate-50 p-2 rounded bg-slate-800">
            <div class="season lg:w-[800px] mx-auto mb-5">
                <div class="poster relative w-[200px] md:h-[300px] mx-auto mt-5">
                    <picture>
                        ${data.poster_path ? `
                            <source media="(max-width: 600px)" srcset="https://image.tmdb.org/t/p/w185${data.poster_path}">
                            <img class="object-cover w-full h-full rounded text-slate-50" src="https://image.tmdb.org/t/p/w342${data.poster_path}" alt="poster of ${data.title ? data.title : data.name}" loading="lazy">
                        ` : `
                            <source media="(max-width: 600px)" srcset="./assets/not-found-poster-medium.webp">
                            <img class="object-cover w-full h-full rounded text-slate-50" src="./assets/not-found-poster-large.webp" alt="poster of ${data.title ? data.title : data.name}" loading="lazy">
                        `}
                        
                    </picture>
                    ${data.air_date ? `
                        <span class="absolute top-0 right-0 rounded-tr rounded-bl text-xs bg-red-600 py-1 px-2">${data.air_date.slice(0, 4)}</span>
                    ` : ''}
                </div>
                <div class="title text-center mt-5">
                    <h1 class="font-bold">${data.name}</h1>
                    <i class="text-xs">Season ${data.season_number}</i>
                </div>
                <div class="overview mt-5">
                    ${data.overview ? `
                            <p class="text-justify leading-6 indent-5">${data.overview}</p>
                        ` : '<i class="text-center block mx-auto leading-6 font-bold">Sorry!!! we couldnt find an overview for this season</i>'}
                </div>
            </div>
            <div class="episode">
                <div class="title-h2">
                    <h1 class="font-bold text-lg">List Episode : ${data.episodes.length} Episode</h1>
                </div>
                <div class="list grid grid-cols-2 gap-2 md:grid-cols-3 overflow-y-scroll scrollbar-custom pr-2 h-[700px] md:h-[1000px]">
                    ${data.episodes.map((episode) => `
                        <div class="bg-slate-700 relative rounded">
                            <picture class="relative">
                                ${episode.still_path ? `
                                    <source media="(max-width: 600px)" srcset="https://image.tmdb.org/t/p/w300${episode.still_path ? episode.still_path : ''}">
                                    <img class="object-cover h-[200px] w-full text-slate-50 rounded" src="https://image.tmdb.org/t/p/original${episode.still_path ? episode.still_path : ''}" alt="poster of ${episode.name}" loading="lazy">
                                ` : `
                                    <source media="(max-width: 600px)" srcset="./assets/not-found-banner-still.webp">
                                    <img class="object-cover object-left-bottom h-[200px] w-full text-slate-50 rounded" src="./assets/not-found-banner-still.webp" alt="poster of ${episode.name}" loading="lazy">
                                `}
                                
                            </picture>
                            <span class="text-xs absolute top-0 right-0 bg-red-600 py-1 px-2 rounded-tr rounded-bl">Eps. ${episode.episode_number}</span>
                            <div class="p-2 text-center">
                                <h1 class="font-bold">${episode.name}</h1>
                                ${episode.overview ? `
                                    <p class="text-xs lg:text-sm mt-3 text-justify leading-5 indent-5">${episode.overview}</p>
                                ` : '<i class="text-xs mt-3 text-justify">Sorry!!! we couldnt find an overview for this episode</i>'}
                            </div>
                        </div>
                    `).join('')}
                </div>
                </div>
            </div>
        </div>
    `;

const traillerTemplate = (data) => `
        <iframe class="w-[400px] h-[200px] md:w-[500px] md:h-[300px] rounded" src="https://www.youtube.com/embed/${data.key}" title="${data.name}" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen loading="lazy"></iframe>
    `;

const detailMovieOrTvTemplate = (data, cast, trailers) => `
        <div class="w-full rounded mb-5">
            <div class="backdrop-image relative overflow-hidden before:absolute before:w-full before:h-[101%] before:bg-gradient-to-b before:from-slate-900 before:via-slate-900/10 before:to-slate-900 before:top-0 before:left-0 before:z-10">
                <picture>
                    ${data.backdrop_path ? `
                        <source media="(max-width: 768px)" srcset="https://image.tmdb.org/t/p/w780${data.backdrop_path}">
                        <source media="(max-width: 1444px)" srcset="https://image.tmdb.org/t/p/w1280${data.backdrop_path}">
                        <img class="object-cover w-full min-h-[200px] text-slate-50 rounded-[inherit]" src="https://image.tmdb.org/t/p/w1280${data.backdrop_path}" alt="image of ${data.title ? data.title : data.name}" loading="lazy">
                    ` : `
                        <source media="(max-width: 768px)" srcset="./assets/not-found-banner-medium.webp">
                        <source media="(max-width: 1444px)" srcset="./assets/not-found-banner-large.webp">
                        <img class="object-cover w-full min-h-[200px] text-slate-50 rounded-[inherit]" src="./assets/not-found-banner-large.webp" alt="image of ${data.title ? data.title : data.name}" loading="lazy">
                    `}
                    
                </picture>
                <div class="title w-[90%] absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 text-center z-20">
                    <h1 class="text-slate-50 font-bold text-xl md:text-2xl lg:text-3xl drop-shadow-2xl">${data.title ? data.title : data.name}</h1>
                </div>
            </div>
            <div class="description bg-slate-800 w-full text-slate-50 p-2">
                <div class="mt-3">
                    <div class="title-h2">
                        <h2 class="text-lg font-bold">Detail</h2>
                    </div>
                    <div class="detail text-sm">
                        <table class="">
                            <tbody>
                                <tr>
                                    <td>Genre</td>
                                    <td class="flex flex-wrap gap-1 border-none">${data.genres.length >= 1 ? data.genres.map((genre) => `<a class="bg-red-600 flex items-center justify-center p-1 text-center rounded hover:bg-red-500 transition-all" href="#/genre/${genre.id}" aria-label="${genre.name}">${genre.name}</a>`).join('') : '-'}</td>
                                </tr>
                                <tr>
                                    <td>Rating</td>
                                    <td>${data.vote_average ? `${String(data.vote_average).substring(0, 3)}/10` : '-'}</td>
                                </tr>
                                ${data.release_date ? `
                                    <tr>
                                        <td>Date</td>
                                        <td>${data.release_date ? data.release_date : '-'}</td>
                                    </tr>
                                ` : `
                                    <tr>
                                        <td>First Episode On Air</td>
                                        <td>${data.first_air_date ? data.first_air_date : '-'}</td>
                                    </tr>
                                    <tr>
                                        <td>Last Episode On Air</td>
                                        <td>${data.last_air_date ? data.last_air_date : '-'}</td>
                                    </tr>
                                `}
                                <tr>
                                    <td>Duration</td>
                                    <td>${data.runtime > 0 ? data.runtime : data.episode_run_time > 0 ? data.episode_run_time : '-'} min</td>
                                </tr>
                                <tr>
                                    <td>Language</td>
                                    <td>${data.spoken_languages.length !== 0 ? data.spoken_languages.map((lang) => lang.english_name).join(' | ') : '-'}</td>
                                </tr>
                                ${data.number_of_seasons ? `
                                    <tr>
                                        <td>Season</td>
                                        <td class="grid gap-1 grid-cols-3 lg:grid-cols-9 border-none">${data.seasons.map((season) => `<a class="bg-red-600 flex items-center justify-center p-1 text-center rounded hover:bg-red-500 transition-all" href="#/tv/${data.id}/season/${season.season_number}" title="${season.name}">${season.name === 'Specials' ? 'Specials' : `Season ${season.season_number}`}</a>`).join('')}</td>
                                    </tr>
                                ` : ''}
                                <tr>
                                    <td>Status</td>
                                    <td>${data.status ? data.status : '-'}</td>
                                </tr>
                                <tr>
                                    <td>Website</td>
                                    <td>${data.homepage ? `<a class="transition-all font-bold hover:text-red-600" href="${data.homepage}" rel="noopener noreferrer" target="_blank">${data.title ? data.title : data.name}</a>` : '-'}</td>
                                </tr>
                                <tr>
                                    <td>Synopsis</td>
                                    <td class="leading-6">${data.overview ? data.overview : '-'}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <div class="cast mt-5">
                    <div class="title-h2 flex items-center justify-between">
                        <h2 class="text-lg font-bold">Cast</h2>
                    </div>
                    <div class="overflow-x-scroll scrollbar-custom scrollbar-hide lg:pb-2">
                        <div class="list-card pt-2 flex gap-2">
                            ${cast.length > 0 ? cast.slice(0, 10).map((person) => cardTemplate(person, 'cast', 'flex')).join('') : '<i>Ups !!! We cant find this information. We are sorry!</i>'}
                        </div>
                    </div>
                </div>
                <div class="trailer mt-5">
                    <div class="title-h2">
                        <h2 class="text-lg font-bold">Trailer</h2>
                    </div>
                    <div class="video overflow-x-scroll scrollbar-custom pb-2">
                        <div class="w-fit flex items-center gap-2">
                            ${trailers.length < 1 ? '<i>Trailer not found in our database. We are sorry!</i>' : trailers.map((trailer) => traillerTemplate(trailer)).join('')}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    `;

const detailPerson = (data) => `
    <div class="bg-slate-800 p-2 mt-5 lg:mt-0 text-slate-50 rounded">
        <div class="name title-h2">
            <h2 class="text-lg font-bold">${data.name ? data.name : ''}</h2>
        </div>
        <div class="lg:flex lg:gap-2 lg:justify-between">
            <figure class="picture w-full text-center mb-5">
                <picture>
                    ${data.profile_path ? `
                        <source media="(max-width: 600px)" srcset="https://image.tmdb.org/t/p/w185${data.profile_path}">
                        <img class="object-cover mx-auto min-w-[150px] max-w-[200px] h-fit text-slate-50 rounded" src="https://image.tmdb.org/t/p/w342${data.profile_path}" alt="poster of ${data.title}" loading="lazy">
                    ` : `
                        <source media="(max-width: 600px)" srcset="./assets/not-found-poster-small.webp">
                        <img class="object-cover mx-auto min-w-[150px] max-w-[200px] h-fit text-slate-50 rounded" src="./assets/not-found-poster-medium.webp" alt="poster of ${data.title}" loading="lazy">
                    `}
                </picture>
                <figcaption>
                    <i class="text-xs">${data.name ? data.name : ''}</i>
                </figcaption>
            </figure>
            <div class="detail ">
                <table class="">
                    <tbody>
                        <tr>
                            <td>Name</td>
                            <td>${data.name ? data.name : '-'}</td>
                        </tr>
                        <tr>
                            <td>Also known as</td>
                            <td>${data.also_known_as.length !== 0 ? data.also_known_as.map((name) => name).join(' | ') : '-'}</td>
                        </tr>
                        <tr>
                            <td>Birthday</td>
                            <td>${data.birthday ? data.birthday : '-'}</td>
                        </tr>
                        ${data.deathday ? `
                            <tr>
                                <td>Death</td>
                                <td>${data.deathday}</td>
                            </tr>
                            ` : ''}
                        <tr>
                            <td>Place of birth</td>
                            <td>${data.place_of_birth ? data.place_of_birth : '-'}</td>
                        </tr>
                        <tr>
                            <td>Biography</td>
                            <td class="leading-6">${data.biography ? data.biography : '-'}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
    `;
export {
  headerTemplateCreator, sliderSplideListItem, asideTemplate, cardTemplate, listTemplate, detailMovieOrTvTemplate, traillerTemplate, detailPerson, seasonTemplate,
};
